import * as React from "react";
import * as _ from "lodash";
import { withRouter, Redirect, Link } from "react-router-dom";
import { hot } from "react-hot-loader";
import { Button, Layout } from 'antd';
import { Drawer, Typography, Row, Col } from 'antd';
const { Content, Footer, Header } = Layout;
import { useAuthState } from "react-firebase-hooks/auth";
import { useState, useContext } from 'react';
import { Modal } from 'antd';
import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth';
import { createRef } from 'react';
import { mapbox } from "../mapbox";
import { auth, firebase, firestore } from "../firebase";
import 'mapbox-gl/dist/mapbox-gl.css';
import { LoginUIContext } from "../providers/LoginUIProvider";

const CreateEvent = ({ history, location, match, value }) => {

    const [user, loading, error] = useAuthState(auth);
    const [isLoginModalVisible, setIsLoginModalVisible] = useContext(LoginUIContext);

    function saveEventHandler() {
        if (!loading && user) {

            const a = firestore.collection('events')
                .add({
                    created: new Date(),
                    points: this.state.eventState.routeCoordinates,
                    summary: this.state.eventState.summary,
                    owner: user.providerId
                });
                console.log(a);
        }
        else {
            setIsLoginModalVisible(true);
        }
    }

    let body = (<></>);
    if (value.isCreatingEvent) {
        let distance = 0;
        for (let [key, instruction] of value.eventState.routeInstructions) {
            distance += instruction.distance;
        }

        body = (
            <Row>
                <Col span={24}>
                    <Typography.Title level={2}>{value.eventState.summary}</Typography.Title>
                </Col>
                <Col span={24}>
                    Distance: {(distance / 1000).toFixed(2)} km
          </Col>
                <Col span={24}>
                    <Button onClick={value.cancelEventCreationHandler}>Cancel</Button>
                    <Button onClick={saveEventHandler}>Save</Button>
                </Col>
            </Row>
        );
    } else {
        if (value.canCreateEvent) {
            body = (
                <Row>
                    <Col span={24}>
                        <Typography.Title level={2}>Explore upcoming hikes or Click to create your event</Typography.Title>
                    </Col>
                </Row>
            );
        }
        else {
            body = (
                <Row>
                    <Col span={24}>
                        <Typography.Title level={2}>Explore upcoming hikes or Zoom in to create your event</Typography.Title>
                    </Col>
                </Row>
            );
        }
    }

    return (
        <>
            {body}
        </>
    );
}

export default hot(module)(withRouter(CreateEvent));